<template>
  <div id="ccheader">
  	<div class="header">
  		<span class="mintui mintui-back" v-if="status!=1" @click="back()"></span>
    	<router-link :to="{name:'test'}" tag="span" class="mintui mintui-back" v-if="status==1"></router-link>
    	<span>{{title}}</span>
      <div class="stepcss">
        <template v-for="(value,index) of stepname">
          <p :class="[status==(index+1)?'hlight':'']">{{value}}</p>
          <p :class="[status==(index+1)?'hlight':'']" v-if="index!=stepnamelength-1">></p>
        </template>
      </div>
   </div>
    <!-- <p :class="[status1==1?'hlight':'']">{{value}}</p>
    <p :class="[status1==1?'hlight':'']" v-if="status1!=2">&nbsp&nbsp></p> -->
    <div id="input">
      <div v-for="(valeu,index) of stepname" v-show="index==status-1">
        <slot :name="index"></slot>
      </div>
    </div>
	  <div class="footer">
      <div v-if="status!=stepnamelength" class="next" @click="++status">下一步</div>
      <router-link :to="{name:'test'}" class="next" v-if="status==stepnamelength">提交</router-link>
    </div>
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

  export default{
    props:["title","stepname",],
  	data(){
  		return{
  			status:'1',
  		}
  	},
    components:{
      MintUI,
      
    },
    computed:{
      stepnamelength(){
        return this.stepname.length;
      }
    },
    methods: {
      back(){
        this.status=this.status-1;
      }
    },
  }

</script>

<style>
#ccheader{
	width: 100%;
}
#ccheader .header{
	width: 100%;
	height: 3.893333rem;
	background-color: #60c0ff;
	padding-top: .506667rem;
}
#ccheader .header span:nth-child(1){
	display: inline-block;
	width: 18%;
	height: .426667rem;
	font-size: .453333rem;
	color: #fff;
	padding-left: 2%;
}
#ccheader .header span:nth-child(2){
	display: inline-block;
	width: 60%;
	height: .426667rem;
	font-family: FZLTHJW--GB1-0;
	font-size: .453333rem;
	color: #ffffff;
	text-align: center;
}
#ccheader .header .stepcss{
	margin-top: .506667rem;
  display: flex;
  justify-content: space-around;
}
#ccheader .header .stepcss p{
	display: inline-block;
	font-family: FZLTHJW--GB1-0;
	font-size: .373333rem;
	color: #d4eeff;
	text-align: center;
}
#ccheader .header .stepcss p.hlight{
	color: #fff;
}
#ccheader{
	position: relative;
}
#ccheader .footer{
  position: absolute;
}
#ccheader .footer{
  width: 92%;
  height: 1.066667rem/* 80px */;
  background-color: #60c0ff;
  box-shadow: 0px .04rem .28rem .066667rem 
    rgba(96, 192, 255, 0.35);
  border-radius: .533333rem/* 40px */;
  top: 14rem;
  left: 4%;
  
}
#ccheader .footer .next{
  display: block;
  font-family: FZLTHJW--GB1-0;
  font-size: .4rem/* 30px */;
  text-align: center;
  line-height: 1.066667rem/* 80px */;
  color: #ffffff;
}
</style>


