import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Home from '../view/Home.vue'
import Contract from '../view/Contract.vue'
import Linkman from '../view/Linkman.vue'
import Addlinkman from '../view/Addlinkman.vue'
import My from '../view/My.vue'
import CCertification from '../components/CCertification.vue'
import CCsubmit from '../view/CCsubmit.vue'
import test from '../view/test.vue'
import test1 from '../view/test1.vue'
import test2 from '../view/test2.vue'
// import Ccinfo from '../view/CCertificationStep/Ccinfo.vue'
// import Lpinfo from '../view/CCertificationStep/Lpinfo.vue'
// import Eminfo from '../view/CCertificationStep/Eminfo.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {name:'Home',path:'/',component:Home},
	{name:'Contract',path:'/contract',component:Contract},
	{name:'Linkman',path:'/linkman',component:Linkman},
	{name:'Addlinkman',path:'/linkman/addlinkman',component:Addlinkman},
	{name:'My',path:'/my',component:My,
		// children:[
		// 	{
		// 		name:'Ccinfo',path:'/my/ccinfo',component:Ccinfo
		// 	},
		// ]
	},
    // {name:'Ccinfo',path:'/my/ccertification/ccinfo',component:Ccinfo},
    // {name:'Lpinfo',path:'/my/ccertification/Lpinfo',component:Lpinfo},
    // {name:'Eminfo',path:'/my/ccertification/Eminfo',component:Eminfo},
    {name:'CCertification',path:'/my/ccertification',component:CCertification},
    {name:'CCsubmit',path:'/my/ccertification/ccsubmit',component:CCsubmit},
    {name:'test',path:'/test',component:test},
    {name:'test1',path:'/test1',component:test1},
    {name:'test2',path:'/test2',component:test2},

  ]
})



