<template>
  <div id="ccer">
  	<div class="header">
  		<span class="mintui mintui-back" v-if="status!=1" @click="back()"></span>
    	<router-link :to="{name:'My'}" tag="span" class="mintui mintui-back" v-if="status==1"></router-link>
    	<span>企业认证</span>
    	<ul>
    		<li :class="[status==1?'hlight':'']">企业信息</li>
    		<li :class="[status==1?'hlight':'']">></li>
    		<li :class="[status==2?'hlight':'']">法人信息</li>
    		<li :class="[status==2?'hlight':'']">></li>
    		<li :class="[status==3?'hlight':'']">授权人信息</li>
    	</ul>
    </div>


	<Ccinfo v-if="status==1" v-bind:cc="cc"></Ccinfo>
	<Lpinfo v-if="status==2" v-bind:lp="lp"></Lpinfo>
	<Eminfo v-if="status==3" v-bind:em="em"></Eminfo>


	<div class="footer">
    	<div v-if="status==1" class="next" @click="status=2">下一步</div>
		<div v-if="status==2" class="next" @click="status=3">下一步</div>	
		<!-- <div v-if="status==3" class="next" @click="send()">提交</div>	 -->
    <router-link :to="{name:'CCsubmit'}" class="next" v-if="status==3">提交</router-link>
    </div>
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import Ccinfo from '../view/CCertificationStep/Ccinfo.vue'
import Lpinfo from '../view/CCertificationStep/Lpinfo.vue'
import Eminfo from '../view/CCertificationStep/Eminfo.vue'
  export default{
  	data(){
  		return{
  			status:'1',
  			// contractinfo:{
  			// 	ccname:'',
  			// 	ccphone:'',
  			// 	ccaddress:'',
  			// 	ccnumber:'',
  			// 	lpname:'',
  			// 	lpphone:'',
  			// 	lpid:'',
  			// 	emname:'',
  			// 	emphone:'',
  			// 	emid:'',
  			// },
  			cc:{
  				name:'',
  				phone:'',
  				address:'',
  				number:'',
  			},
  			lp:{
  				name:'',
  				phone:'',
  				id:'',  			
  			},
  			em:{
  				name:'',
  				phone:'',
  				id:'',
  			}
  		}
  	},
    components:{
      MintUI,
      Ccinfo,
      Lpinfo,
      Eminfo,
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      send(){
      	console.log('成功');
      },
      back(){
      	this.status=this.status-1;
      }
    },
  }
</script>

<style>
#ccer{
	width: 100%;
}
#ccer .header{
	width: 100%;
	height: 3.893333rem/* 292px */;
	background-color: #60c0ff;
	padding-top: .506667rem/* 38px */;
}
#ccer .header span:nth-child(1){
	display: inline-block;
	width: 18%;
	height: .426667rem/* 32px */;
	font-size: .453333rem/* 34px */;
	color: #fff;
	padding-left: 2%;
}
#ccer .header span:nth-child(2){
	display: inline-block;
	width: 60%;
	height: .426667rem/* 32px */;
	font-family: FZLTHJW--GB1-0;
	font-size: .453333rem/* 34px */;
	color: #ffffff;
	text-align: center;
}
#ccer .header ul{
	margin-top: .506667rem/* 38px */;
}
#ccer .header ul li{
	display: inline-block;
	font-family: FZLTHJW--GB1-0;
	font-size: .373333rem/* 28px */;
	color: #d4eeff;
	text-align: center;
}
#ccer .header ul li.hlight{
	color: #fff;
}
#ccer .header ul li:nth-child(1){
	width: 28%;
}
#ccer .header ul li:nth-child(2){

	width: 3%;
}
#ccer .header ul li:nth-child(3){
	width: 28%;
}
#ccer .header ul li:nth-child(4){
	width: 3%;
}
#ccer .header ul li:nth-child(5){
	width: 30%;
}
#ccer{
	position: relative;
}


#ccer .footer{
	position: absolute;
}
#ccer .footer{
	width: 92%;
	height: 1.066667rem/* 80px */;
	background-color: #60c0ff;
	box-shadow: 0px .04rem .28rem .066667rem 
		rgba(96, 192, 255, 0.35);
	border-radius: .533333rem/* 40px */;
	top: 14rem;
	left: 4%;
	
}
#ccer .footer .next{
	display: block;
	font-family: FZLTHJW--GB1-0;
	font-size: .4rem/* 30px */;
	text-align: center;
	line-height: 1.066667rem/* 80px */;
	color: #ffffff;
}
</style>


