<template>
<div id="home">
	<div class="content">
	    <div class="header">
	    区块链合同
	    </div>
	    <div class="nav">
		  	<ul>
		  		<li v-for="item in items"><img :src="item.path" alt="">
		  			<div>{{item.name}}</div>
		  		</li>
		  	</ul>
  	   </div>
    </div>
  <tab></tab>
</div>
</template>

<script>
import tab from '@/components/tab.vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
export default {
	data(){
		return{
			items:[{
				path:require('../assets/drawable-hdpi/上传合同.png'),
				name:'上传合同'
			},{
				path:require('../assets/drawable-hdpi/模板创建.png'),
				name:'模板创建'
			},{
				path:require('../assets/drawable-hdpi/草稿箱.png'),
				name:'草稿箱'
			},{
				path:require('../assets/drawable-hdpi/待修改.png'),
				name:'待修改'
			},{
				path:require('../assets/drawable-hdpi/待签署.png'),
				name:'待签署'
			},{
				path:require('../assets/drawable-hdpi/已签署.png'),
				name:'已签署'
			},{
				path:require('../assets/drawable-hdpi/已生效.png'),
				name:'已生效'
			},{
				path:require('../assets/drawable-hdpi/已失效.png'),
				name:'已失效'
			}]
		}
	},
	components:{
		tab,
		MintUI,
	}, 
}
</script>

<style>
#home .content{
  width: 100%/* 720px */;
 /* 1129px */
  background-color: #60c0ff;
  padding-bottom: 6%;
  height: 15.46rem;
  /*height: 90%;*/
}
#home .header{
	width: 100%/* 200px */;
/* 102px */
	font-family: FZLTHJW--GB1-0;
	font-size: .853333rem/* 34px */;
	color: #ffffff;
	line-height: 1.806667rem/* 458px */;
	/*margin:0 auto;*/
	text-align: center;
}
#home .nav{
	width: 90%/* 660px */;
	background-color: #fff;
	/* 1000px */
	margin:0 auto;
	border-radius: .26rem/* 12px */;
}
#home .nav ul li{
	list-style: none;
	width: 49%/* 329px */;
	/* 248px */
	display: inline-block;
	/*float:left;*/
	cursor: pointer;
	padding-bottom: 5%;
}
#home .nav ul li img{
	width: 50%;
	margin-top:11%;
	/*margin-bottom: 1rem;*/
	/* 62px */
	margin-left: 25%;
	/* 105px */
}
#home .nav ul li div{
	/*display: inline-block;*/
	/*width: 4.373333rem;*/
	/* 328px */
	width: 100%;
	font-family: FZLTHJW--GB1-0;
	font-size: .4rem/* 28px */;
	color: #666666;
	line-height: .22rem/* 24px */;
	text-align: center;
}
</style>


