<template>
  <div id="linkman">
  	<div class="header">
  		联系人
  		<router-link :to="{name:'Addlinkman'}">
  			<img src="../assets/drawable-hdpi/添加联系人.png" alt="">
  		</router-link>
  	</div>
    <div class="content">
    	<mt-navbar v-model="selected">
  			<mt-tab-item id="1">个人</mt-tab-item>
			<mt-tab-item id="2">企业</mt-tab-item>
		</mt-navbar>
		<div class="stance"></div>

<!-- tab-container -->
		<mt-tab-container v-model="selected">
  			<mt-tab-container-item id="1">
    			<ul>
    				<li v-for="item in items1">
    					<!-- <img src="../assets/drawable-hdpi/联系人-头像.png" alt=""> -->
    					<p>{{item.name}}</p>
						<div v-if="item.status==1">已实名</div>
						<div v-if="item.status==2">已认证</div>
    					<p class="num">{{item.number}}</p>
    				</li>
    			</ul>
  			</mt-tab-container-item>
  			<mt-tab-container-item id="2">
    			<ul>
    				<li v-for="item in items2">
    					<!-- <img src="../assets/drawable-hdpi/联系人-头像.png" alt=""> -->
    					<p>{{item.name}}</p>
						<div v-if="item.status==1">已实名</div>
						<div v-if="item.status==2">已认证</div>
    					<p class="num">{{item.number}}</p>
    				</li>
    			</ul>
  			</mt-tab-container-item>
		</mt-tab-container>
    </div>
    <tab></tab>
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import tab from '@/components/tab.vue'
import axios from 'axios'
export default {
	data(){
		return{
		selected:'1',
		items1:[],
		items2:[],
		}
	},
	components:{
		tab,
		MintUI,
		axios,
		},
	mounted(){
		axios.get("/api/linkman1",{params:{}}).then(res => {
	      // console.log("no1:", JSON.stringify(res.data.));
	      this.items1 = res.data.data;
	    });
		axios.get("/api/linkman2",{params:{}}).then(res => {
	      // console.log("no1:", JSON.stringify(res.data.));
	      this.items2 = res.data.data;
	    });
	}
}
</script>

<style>
#addlinkman {
  width: 100%/* 720px */;
}
#linkman .header{
  height: 1.7rem;
  background-color: #60c0ff;  
  font-family: FZLTHJW--GB1-0;
  font-size: .453333rem/* 34px */;
  color: #ffffff;
  text-align: center;
  line-height: 1.7rem;
  position: relative;
}
#linkman .header img{
	height: 50%;
	position: absolute;
	left: 88%;
	top: 25%;
}
#linkman .content .mint-navbar{
  height: 1.333333rem/* 100px */;
  color: #c6e5fa;
  background-color: #60c0ff;
  border: 0px;
}
#linkman .content .mint-tab-item-label{
  font-size: .373333rem/* 28px */;
}
#linkman .content .mint-tab-item.is-selected{
  color: #ffffff;
}
#linkman .content{
  height: 14.36rem;
  background-color: #fff;
  position: relative;
}
#linkman .content .stance{
  height: 2rem;
  background-color: #60c0ff;
}
#linkman .content .mint-navbar .mint-tab-item.is-selected{
  border-bottom: 2px solid #fff;
}
a:hover{
  text-decoration: none;
}
#linkman .content .mint-tab-container{
  width: 94%;
  background-color: #fff;
  position: absolute;
  left: 3%;
  top:16%;
}
#linkman .content .mint-tab-container-item li{
	height: 1.6rem/* 120px */;
	/*background-color: #ccc;*/
	box-shadow: 0px 3px 21px 5px 
		rgba(96, 192, 255, 0.35);
	border-radius: .133333rem/* 10px */;
	margin-bottom: .266667rem/* 20px */;
	background: url("../assets/drawable-hdpi/联系人-头像.png") no-repeat 2% center;
	background-size: 1.6rem;
	padding-left: 20%; 
}
/*#linkman .content .mint-tab-container-item li img{
	display: inline-block;
	height: 70%;
	margin-top: 3%;
	margin-left: 3%;
}*/
#linkman .content .mint-tab-container-item li p{
	display: inline-block;
	height: .8rem/* 60px */;
	font-family: FZLTHJW--GB1-0;
	font-size: .426667rem/* 32px */;
	line-height: 1rem;
	color: #1491e2;
}
#linkman .content .mint-tab-container-item li .num{
	display: block;

}
#linkman .content .mint-tab-container-item li div{
	display: inline-block;
	font-family: FZLTHJW--GB1-0;
	background-color: #60c0ff;
	font-size: .32rem/* 24px */;
	color: #ffffff;
	line-height: .7rem;
	width: 1.3rem;
	text-align: center;
	border-radius: .35rem;
}
</style>



