<template>
  <div id="contract">
    <div class="content">
    <div class="header">
      <!-- <img src="../assets/drawable-hdpi/搜索.png" alt=""> -->
      <input type="text" name="search" placeholder="合同名称或合同编号">
    </div>
    <div class="c-content">
    	<mt-navbar v-model="selected" class="nav">
  			<mt-tab-item id="1">全部</mt-tab-item>
			  <mt-tab-item id="2">带签署</mt-tab-item>
			  <mt-tab-item id="3">已签署</mt-tab-item>
			  <mt-tab-item id="4">待修改</mt-tab-item>
			  <mt-tab-item id="5">已生效</mt-tab-item>
			  <mt-tab-item id="6">已失效</mt-tab-item>
		  </mt-navbar>
      <div class="stance"></div>

<!-- tab-container -->
		<mt-tab-container v-model="selected">
  			<mt-tab-container-item id="1">
    			<ul>
    				<li v-for="item in items">
    					<span>{{item.number}}</span>
    					<span class="state">{{item.status}}</span>
    					<p>{{item.name}}</p>
    					<p class="time">{{item.time}}</p>
    					<img src="../assets/drawable-hdpi/下拉.png" v-if="!item.bool"  @click="item.bool=true">
    					<img src="../assets/drawable-hdpi/收回.png" v-if="item.bool"  @click="item.bool=false">
    					<div v-for="msg in item.obj" v-if="item.bool">
    						<span>{{msg.number}}</span>
    						<span class="state">{{msg.status}}</span>
    						<p>{{msg.name}}</p>
    						<p class="time">{{msg.time}}</p>
    					</div>
    				</li>
    			</ul>
  			</mt-tab-container-item>
  			<mt-tab-container-item id="2">
    			<ul>
            <li v-for="item in items" v-if="item.status=='待签署'">
      
                <span>{{item.number}}</span>
                <span class="state">{{item.status}}</span>
                <p>{{item.name}}</p>
                <p class="time">{{item.time}}</p>
                <img src="../assets/drawable-hdpi/下拉.png" v-if="!item.bool"  @click="item.bool=true">
                <img src="../assets/drawable-hdpi/收回.png" v-if="item.bool"  @click="item.bool=false">
                <div v-for="msg in item.obj" v-if="item.bool">
                  <span>{{msg.number}}</span>
                  <span class="state">{{msg.status}}</span>
                  <p>{{msg.name}}</p>
                  <p class="time">{{msg.time}}</p>
                </div>
              
            </li>  
          </ul>
  			</mt-tab-container-item>
  			<mt-tab-container-item id="3">
    			<ul>
            <li v-for="item in items" v-if="item.status=='已签署'">
      
                <span>{{item.number}}</span>
                <span class="state">{{item.status}}</span>
                <p>{{item.name}}</p>
                <p class="time">{{item.time}}</p>
                <img src="../assets/drawable-hdpi/下拉.png" v-if="!item.bool"  @click="item.bool=true">
                <img src="../assets/drawable-hdpi/收回.png" v-if="item.bool"  @click="item.bool=false">
                <div v-for="msg in item.obj" v-if="item.bool">
                  <span>{{msg.number}}</span>
                  <span class="state">{{msg.status}}</span>
                  <p>{{msg.name}}</p>
                  <p class="time">{{msg.time}}</p>
                </div>
              
            </li>  
          </ul>
  			</mt-tab-container-item>
  			<mt-tab-container-item id="4">
    			<ul>
            <li v-for="item in items" v-if="item.status=='待'">
      
                <span>{{item.number}}</span>
                <span class="state">{{item.status}}</span>
                <p>{{item.name}}</p>
                <p class="time">{{item.time}}</p>
                <img src="../assets/drawable-hdpi/下拉.png" v-if="!item.bool"  @click="item.bool=true">
                <img src="../assets/drawable-hdpi/收回.png" v-if="item.bool"  @click="item.bool=false">
                <div v-for="msg in item.obj" v-if="item.bool">
                  <span>{{msg.number}}</span>
                  <span class="state">{{msg.status}}</span>
                  <p>{{msg.name}}</p>
                  <p class="time">{{msg.time}}</p>
                </div>
              
            </li>  
          </ul>
  			</mt-tab-container-item>
  			<mt-tab-container-item id="5">
    			<ul>
            <li v-for="item in items" v-if="item.status=='已生效'">
      
                <span>{{item.number}}</span>
                <span class="state">{{item.status}}</span>
                <p>{{item.name}}</p>
                <p class="time">{{item.time}}</p>
                <img src="../assets/drawable-hdpi/下拉.png" v-if="!item.bool"  @click="item.bool=true">
                <img src="../assets/drawable-hdpi/收回.png" v-if="item.bool"  @click="item.bool=false">
                <div v-for="msg in item.obj" v-if="item.bool">
                  <span>{{msg.number}}</span>
                  <span class="state">{{msg.status}}</span>
                  <p>{{msg.name}}</p>
                  <p class="time">{{msg.time}}</p>
                </div>
              
            </li>  
          </ul>
  			</mt-tab-container-item>
  			<mt-tab-container-item id="6">
    			<ul>
            <li v-for="item in items" v-if="item.status=='已失效'">
      
                <span>{{item.number}}</span>
                <span class="state">{{item.status}}</span>
                <p>{{item.name}}</p>
                <p class="time">{{item.time}}</p>
                <img src="../assets/drawable-hdpi/下拉.png" v-if="!item.bool"  @click="item.bool=true">
                <img src="../assets/drawable-hdpi/收回.png" v-if="item.bool"  @click="item.bool=false">
                <div v-for="msg in item.obj" v-if="item.bool">
                  <span>{{msg.number}}</span>
                  <span class="state">{{msg.status}}</span>
                  <p>{{msg.name}}</p>
                  <p class="time">{{msg.time}}</p>
                </div>
              
            </li>  
          </ul>
  			</mt-tab-container-item>
		</mt-tab-container>
    </div>
    </div>
    <tab></tab>
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import tab from '@/components/tab.vue'
import axios from 'axios'
export default {
	data(){
		return{
      value:'',
			selected:'1',
			bool:false,
			items:[],
		}
	},
	components:{
		tab,
		MintUI,
		axios,
	},
	mounted: function(){
	    axios.get("/api/contract",{params:{}}).then(res => {
	      // console.log("no1:", JSON.stringify(res.data.data));

	      this.items = res.data.data;
	      console.log(this.items[0].obj);
	      
	    });
	},
}
</script>

<style>
#contract .content {
   width: 100%/* 720px */;
 /* 1129px */

  /*padding-top: .213333rem;*/
  /*height: 14rem;*/
  overflow: scroll;
}
#contract .header{
  height: 1.6rem;
  background-color: #60c0ff;
  padding-top: .4rem;
}
#contract .header input{
  border-radius: .7rem;
  border: 1px solid #fff;
  display: block; 
  margin:0 auto;
  background: url('../assets/drawable-hdpi/搜索.png') no-repeat;
  background-position: .1rem center;
  height: 1.36667rem;
  width: 8.333333rem;
  padding-left: 1.2rem;
  color: #c6e5fa;
}
input::-webkit-input-placeholder{
            color: #c6e5fa;
}
#contract .c-content .mint-navbar.nav{
  height: 1.333333rem/* 100px */;
  color: #c6e5fa;
  background-color: #60c0ff;
}
#contract .c-content .mint-tab-item-label{
  font-size: .373333rem/* 28px */;
}
#contract .c-content .mint-tab-item.is-selected{
  color: #ffffff;
}
#contract .c-content{
  height: 14.02rem;
  background-color: #fff;
  position: relative;
}
#contract .c-content .stance{
  height: 2rem;
  background-color: #60c0ff;
}
#contract .c-content .mint-navbar .mint-tab-item.is-selected{
  border-bottom: 2px solid #fff;
}
a:hover{
  text-decoration: none;
}
#contract .c-content .mint-tab-container{
  width: 94%;
  position: absolute;
  left: 3%;
  top:14%;
}
#contract .c-content .mint-tab-container-item li{
  /*height: 3rem;*/
  background-color: #fff;
  margin-bottom: .4rem;
  box-shadow: 0px 3px 21px 5px 
    rgba(96, 192, 255, 0.35);
  border-radius: 10px;
}
#contract .c-content .mint-tab-container-item li span{
  display: inline-block;
  height: .8rem/* 60px */;
  text-align: center;
  line-height: .8rem;
  font-size: .373333rem/* 28px */;
  color: #808080;
  font-family: FZLTHJW--GB1-0;
  margin-right: 61%;
}
#contract .c-content .mint-tab-container-item li .state{
  /*width: 84px;*/
  margin-right: 0;
  height: .8rem;
  font-family: FZLTHJW--GB1-0;
  font-size: .373333rem/* 28px */;
  color: #f1325f;
}
#contract .c-content .mint-tab-container-item div .state{
  color: #666666;
}
#contract .c-content .mint-tab-container-item li img{
  width: 100%;
}
#contract .c-content .mint-tab-container-item li p{
  height: .8rem/* 60px */;
  line-height: .8rem;
  font-family: FZLTHJW--GB1-0;
  font-size: .426667rem/* 32px */;
  color: #1491e2;
}
#contract .c-content .mint-tab-container-item li .time{
  height: .8rem;
  font-family: FZLTHJW--GB1-0;
  font-size: .32rem/* 24px */;
  line-height: .8rem;
  color: #808080;
}
</style>


