<template>
	<div>
		<el-upload
	  	class="post avatar-uploader"
			action="https://jsonplaceholder.typicode.com/posts/"
			:show-file-list="false"
			:on-success="handleAvatarSuccess"
			:before-upload="beforeAvatarUpload">
	    <img v-if="imageUrl" :src="imageUrl" class="avatar">
	    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
		</el-upload>
	</div>
</template>

<script>
	import MintUI from 'mint-ui'
	import 'mint-ui/lib/style.css'
  export default{
     data(){
  		return{
  			imageUrl: ''
  		}
  	},
    components:{
      MintUI,
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    },
  }
</script>

<style>
	#input .post {
	width: 25%;
	padding-top: .2rem;
	display: inline-block;
}
#input .post.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: .08rem;
    cursor: pointer;
    overflow: hidden;
    /*margin-left: 25%;*/
  }
#input .post.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
#input .post .avatar-uploader-icon {
    font-size: .373333rem;
    color: #8c939d;
    width: 2.3rem;
    height: 1.866667rem;
    line-height: 1.866667rem;
    text-align: center;
  }
#input .post .avatar {
    width: 100%; 
    height: 1.866667rem;
    /*display: block;*/
  }
#input .post span{
	display: inline-block;
	width: 100%;
	font-size: .373333rem;
	text-align: center;
	color: #333333;
}
</style>


