			<mt-field label="企业地址" placeholder="请输入企业地址" v-model="cc.address"></mt-field>
			<mt-field label="营业执照" placeholder="请输入营业执照号码" v-model="cc.number"></mt-field> -->
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
  export default{
    props:["label","text",],
  	data(){
  		return{

  		}
  	},
    components:{
      MintUI,
    },
    methods: {
     
    },
  }
</script>

<style>
#input{
  width: 92%;
  position: absolute;
  top: 70%;
  left: 4%;
}

#input {
	height: 9rem;
	box-shadow: 0px .04rem .28rem .066667rem 
		rgba(96, 192, 255, 0.35);
	border-radius: .133333rem;
}
#input  .mint-cell-wrapper{
	font-size: .373333rem;
}
a:hover{
  text-decoration: none;
}
#input .mint-cell-text{
  font-family: FZLTHJW--GB1-0;
  font-size: .373333rem;
  color: #333333;
}
#input input::-webkit-input-placeholder{
  font-family: FZLTHJW--GB1-0;
  font-size: .32rem;
  color: #808080;
}
</style>


