<template>
  <div id="test1">
    <my-dialog :isShow="isShow" @on-close="closeDialog('isShow')"></my-dialog>
  	<div class="header">
  		<span class="mintui mintui-back" v-if="status1!=1" @click="back()"></span>
    	<router-link :to="{name:'My'}" tag="span" class="mintui mintui-back" v-if="status1==1"></router-link>
    	<span>企业认证</span>

        <total-header :number="3" :status1="status1">
          <p slot="step1">第一步</p>
          <p slot="mark1">></p>
          <p slot="step2">第二部</p>
          <p slot="mark2">></p>
          <p slot="step3">第三步</p>
          <p slot="mark3">></p>
          <!-- <p slot="step4">第四步</p>
          <p slot="mark4">></p>
          <p slot="step5">第五步</p>
          <p slot="mark5">></p> -->
          <p slot="1">
           <inputter label="电话" text="亲输入电话号码"></inputter>
           <inputter label="haoma" text="输入电话号码"></inputter>
           <inputter label="key" text="输入密钥"></inputter>
         </p> 
        <p slot="2">2</p> 
        <p slot="3">3</p> 
        <!-- <p slot="4">4</p> 
        <p slot="5">5</p> --> 
        </total-header>
          
        
        
          <!-- <sec-header :status1="1" value="第二步" step="2" mark="3">
            <p slot="2">第二步</p>
            <p slot="3">></p>
          </sec-header>
       
        
          <sec-header :status1="1" value="第三步" step="3">
            <p slot="3">第三步</p>
          </sec-header> -->
      
    </div>



    
    <div class="footer">
      <div class="next" @click="increase">下一步</div>
      <my-dialog :isShow="isShow" @on-close="closeDialog('isShow')"></my-dialog>
      <!-- <router-link :to="{name:'test'}" class="next" >提交</router-link> -->
    </div>
    
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import Inputter from '../components/Inputter.vue'
import totalHeader from '../components/totalHeader.vue'
import MyDialog from '../components/Dialog.vue'
  export default{
  	data(){
  		return{
  			status1:"1",
        title:'企业认证',
        isShow: false,
  		}
  	},
    components:{
      MintUI,
      Inputter,
      totalHeader,
      MyDialog,
    },
    methods: {
      send(){
      	console.log('成功');
      },
      back(){
      	this.status1=this.status1-1;
      },
      increase(){
        ++this.status1;
       
        console.log(this.status1);
        this.isShow= true;
      },
      closeDialog (attr) {
        this[attr] = false;
      },
    },
  }
</script>

<style>
#test1{
	width: 100%;
}
#test1 .header{
	width: 100%;
	height: 3.893333rem/* 292px */;
	background-color: #60c0ff;
	padding-top: .506667rem/* 38px */;
}
#test1 .header .mintui.mintui-back{
	display: inline-block;
	width: 18%;
	height: .426667rem/* 32px */;
	font-size: .453333rem/* 34px */;
	color: #fff;
	padding-left: 2%;
}
#test1 .header span:nth-child(2){
	display: inline-block;
	width: 60%;
	height: .426667rem/* 32px */;
	font-family: FZLTHJW--GB1-0;
	font-size: .453333rem/* 34px */;
	color: #ffffff;
	text-align: center;
}
#test1 .header .stepcss1{
  margin-top: .506667rem;
  display: flex;
  justify-content: space-around;
}
#test1 .header #ccheader{
	/*display: inline-block;*/
	font-family: FZLTHJW--GB1-0;
	font-size: .373333rem/* 28px */;
	color: #d4eeff;
	text-align: center;
}

#test1 #totalheader .hlight{
	color: #fff;
}

#test1{
	position: relative;
}
#test1 .footer{
  position: absolute;
}
#test1 .footer{
  width: 92%;
  height: 1.066667rem;
  background-color: #60c0ff;
  box-shadow: 0px .04rem .28rem .066667rem 
    rgba(96, 192, 255, 0.35);
  border-radius: .533333rem;
  top: 14rem;
  left: 4%;
  
}
#test1 .footer .next{
  display: block;
  font-family: FZLTHJW--GB1-0;
  font-size: .4rem;
  text-align: center;
  line-height: 1.066667rem;
  color: #ffffff;
}
</style>


