<template>
  <div id="addlinkman">
    <div class="header">
      <router-link :to="{name:'Linkman'}"><</router-link>
      <span>添加联系人</span>
    </div>
    <div class="nav">
    	<div class="inp">
  	  	<mt-field label="姓名" placeholder="请输入真实名称或公司名称"></mt-field>
  			<mt-field label="账号" placeholder="请输入联系人账号" type="tel"></mt-field>
  			<mt-field label="分类" placeholder="请选择联系人类别"></mt-field>
	  	</div>
	  	<div class="footer">
			 <div class="next">提交</div>
	  	</div>
    </div>
    
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
  export default{
  	data(){
  		return{
  			
  		}
  	},
    components:{
      MintUI,
    },
  }
</script>

<style>
#addlinkman {
  width: 100%;
  position: relative;
}
#addlinkman .header{
  height: 2.666667rem/* 200px */;
  background-color: #60c0ff;
}
a:hover {
  text-decoration:none;
}
#addlinkman .header a{
  display: inline-block;
  height: 1.6rem/* 120px */;
  width: 10%;
  font-family: FZLTHJW--GB1-0;
  font-size: .453333rem;
  line-height: 1.6rem;
  text-align: center;
  color: #ffffff;
}
#addlinkman .header span{
  display: inline-block;
  height: 1.6rem;
  width: 80%;
  font-family: FZLTHJW--GB1-0;
  font-size: .453333rem;
  line-height: 1.6rem;
  text-align: center;
  color: #ffffff;
}
#addlinkman .nav{
  position: absolute;
  top:55%;
  left:4%;
  width: 92%;
  height: 4rem/* 580px */;
  box-shadow: 0px .04rem .28rem .066667rem 
    rgba(96, 192, 255, 0.35);
  border-radius: .133333rem/* 10px */;
}
#addlinkman .nav .inp .mint-cell-wrapper{
  font-size: .373333rem/* 28px */;
}
 #addlinkman .nav .inp input::-webkit-input-placeholder{
  font-size: .32rem/* 24px */;
  color: #808080;
}
#addlinkman .footer{
  position: absolute;
}
#addlinkman .footer{
  width: 92%;
  height: 1.066667rem/* 80px */;
  background-color: #60c0ff;
  box-shadow: 0px .04rem .28rem .066667rem 
    rgba(96, 192, 255, 0.35);
  border-radius: .533333rem/* 40px */;
  top: 5rem;
  left: 4%;
  
}
#addlinkman .footer .next{
  display: block;
  font-family: FZLTHJW--GB1-0;
  font-size: .4rem/* 30px */;
  text-align: center;
  line-height: 1.066667rem/* 80px */;
  color: #ffffff;
}
</style>


