<template>
  <div id="totalheader">
    <div class="stepcss">
      <div v-for="n of number" :class="[status1==n?'hlight':'']">
        <template :n="n">
          <slot :name="'step'+n"></slot>
          <slot :name="'mark'+n" v-if="n!=number">></slot>
        </template>
      </div>
    </div>


    <div class="totalinput">
      <div v-for="n of number" v-show="n==status1">
        <slot :name="n"></slot>
      </div>
    </div>

  </div>
     
</template>

<script>


  export default{
    props:["number","status1",],
  	data(){
  		return{

  		}
  	},
    components:{
  
    },
    computed:{

    },
    methods: {

    },
  }

</script>

<style>
#totalheader{
  width: 100%;
  position: relative;
}
#totalheader .stepcss .hlight {
  color: #fff;
}
#totalheader .stepcss {
  width: 100%;
  margin-top: .506667rem;
  display: flex;
  justify-content: space-around;
}
#totalheader .stepcss div{
  display: flex;
  flex: 1;
  justify-content: space-between;
  font-family: FZLTHJW--GB1-0;
  font-size: .373333rem;
  color: #d4eeff;
  text-align: center;
}
#totalheader .stepcss div p:nth-child(1){
  flex: auto;
}
#totalheader .stepcss div:nth-last-child(1) p{
  flex: auto;
}
#totalheader .totalinput{
  width: 92%;
  position: absolute;
  top: 300%;
  left: 4%;
}

#totalheader .totalinput {
  height: 9rem;
  background-color: #fff;
  box-shadow: 0px .04rem .28rem .066667rem 
    rgba(96, 192, 255, 0.35);
  border-radius: .133333rem;
}
#totalheader .totalinput .mint-cell-wrapper{
  font-size: .373333rem;
}
a:hover{
  text-decoration: none;
}
#totalheader .totalinput .mint-cell .mint-cell-text{
  font-family: FZLTHJW--GB1-0;
  font-size: .373333rem;
  color: #333333;
}
#totalheader .totalinput input::-webkit-input-placeholder{
  font-family: FZLTHJW--GB1-0;
  font-size: .32rem;
  color: #808080;
}
</style>


