<template>
  <div id="ccinfo">
    <!-- <div class="header">
    	<router-link :to="{name:'My'}" tag="span" class="mintui mintui-back"></router-link>
    	<span>企业认证</span>
    	<ul>
    		<li>企业信息</li>
    		<li>></li>
    		<li>法人信息</li>
    		<li>></li>
    		<li>授权人信息</li>
    	</ul>
    </div> -->
    <div class="nav">
    	<div class="inp">
	  		<mt-field label="企业名称" placeholder="请输入企业名称" v-model="cc.name"></mt-field>
			<mt-field label="企业电话" placeholder="请输入企业电话" type="tel" v-model="cc.phone"></mt-field>
			<mt-field label="企业地址" placeholder="请输入企业地址" v-model="cc.address"></mt-field>
			<mt-field label="营业执照" placeholder="请输入营业执照号码" v-model="cc.number"></mt-field>
	  	</div>
	  	<div class="post">
	  		<el-upload
  				class="avatar-uploader"
				action="https://jsonplaceholder.typicode.com/posts/"
				:show-file-list="false"
				:on-success="handleAvatarSuccess"
				:before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<span>上传营业执照</span>
	  	</div>
    </div>
    
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
  export default{
    props:["cc"],
  	data(){
  		return{
  			imageUrl: ''
  		}
  	},
    components:{
      MintUI,
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    },
  }
</script>

<style>
#ccinfo{
  width: 100%;
  position: absolute;
  top: 70%;
}
#ccinfo .nav{
	width: 92%;
	height: 9rem/* 580px */;
  margin:0 auto;
	box-shadow: 0px .04rem .28rem .066667rem 
		rgba(96, 192, 255, 0.35);
	border-radius: .133333rem/* 10px */;
}
#ccinfo .nav .inp .mint-cell-wrapper{
	font-size: .373333rem/* 28px */;
}
#ccinfo .nav .post {
	width: 100%;
	padding-top: .2rem;
}
#ccinfo .nav .post .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: .08rem/* 6px */;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin-left: 25%;
  }
#ccinfo .nav .post .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
#ccinfo .nav .post .avatar-uploader-icon {
    font-size: .373333rem/* 28px */;
    color: #8c939d;
    width: 4.6rem/* 690px */;
    height: 1.866667rem/* 140px */;
    line-height: 1.866667rem/* 140px */;
    text-align: center;
  }
#ccinfo .nav .post .avatar {
    width: 100%;
    height: 1.866667rem/* 140px */;
    display: block;
  }
#ccinfo .nav .post span{
	display: inline-block;
	width: 100%;
	font-size: .373333rem/* 28px */;
	text-align: center;
	color: #333333;
}
</style>


