<template>
<div id="tabs">
  <ul>
      <router-link :to="{name:'Home'}" tag="li">
        <img src="../assets/drawable-hdpi/首页-选中.png" v-show="$route.path=='/'">
        <img src="../assets/drawable-hdpi/首页-未选.png" v-show="$route.path!='/'">
        <div >首页</div>
      </router-link>

      <router-link :to="{name:'Contract'}" tag="li">
        <img src="../assets/drawable-hdpi/合同-选中.png" v-show="$route.path=='/contract'">
        <img src="../assets/drawable-hdpi/合同-未选.png" v-show="$route.path!='/contract'">
        <div>合同</div>
      </router-link>
    <router-link :to="{name:'Linkman'}" tag="li">
      <img src="../assets/drawable-hdpi/联系人-选中.png" v-show="$route.path=='/linkman'">
      <img src="../assets/drawable-hdpi/联系人-未选.png" v-show="$route.path!='/linkman'">
      <div>联系人</div>
    </router-link>
    <router-link :to="{name:'My'}" tag="li">
      <img src="../assets/drawable-hdpi/我-选中.png" v-show="$route.path=='/my'">
      <img src="../assets/drawable-hdpi/我-未选.png" v-show="$route.path!='/my'">
      <div>我的</div>
    </router-link>
  </ul>
  
</div>
</template>
<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
  export default{
    data(){
      return{
        selected:'首页',
      }
    },
    components:{
      MintUI,
    }
  }
</script>
<style>
#tabs{
  position: fixed;
  bottom:0;
  /*height: 10%;*/
  background-color: #fff;
  width: 100%;
}
#tabs ul{
  display: inline-block;
  list-style: none;
  width: 100%;
  font-size: 0;
 /* 100px */
  }
#tabs ul li{
  display: inline-block;
  position: relative;
  padding-bottom: 3%;
  width: 25%;
  cursor: pointer;
}
#tabs ul li img{
  width:  50%;
  margin-left: 25%;
}
#tabs ul li div{
  position: absolute;
  top: 68%;
  width: 100%/* 172px */;
  font-family: FZLTHJW--GB1-0;
  font-size: .293333rem/* 22px */;
  text-align: center;
  line-height: .293333rem;
  color: #a2a2a2;
}
#tabs ul .router-link-exact-active div{
  color:#60c0ff;
}
</style>


