<template>
  <div id="lpinfo">
    <div class="nav">
    	<div class="inp">
	  		<mt-field label="法人姓名" placeholder="请输入法人姓名" v-model="lp.name"></mt-field>
			<mt-field label="法人电话" placeholder="请输入法人电话" type="tel" v-model="lp.phone"></mt-field>
			<mt-field label="身份证号" placeholder="请输入法人身份证号码" v-model="lp.id"></mt-field>
	  	</div>
	  	<div class="post">
	  		<div class="right">
	  		<el-upload
  				class="avatar-uploader"
				action="https://jsonplaceholder.typicode.com/posts/"
				:show-file-list="false"
				:on-success="handleAvatarSuccess"
				:before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<span>身份证正面</span>
			</div>
			<div class="left">
			<el-upload
  				class="avatar-uploader left"
				action="https://jsonplaceholder.typicode.com/posts/"
				:show-file-list="false"
				:on-success="handleAvatarSuccess"
				:before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<span>身份证反面</span>
			</div>
	  	</div>
    </div>
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
  export default{
  	props:["lp"],
  	data(){
  		return{
  			imageUrl: ''
  		}
  	},
    components:{
      MintUI,
      ElementUI,
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    },
  }
</script>

<style>
#lpinfo{
	width: 100%;
	position: absolute;
	top: 70%;
}
#lpinfo .nav{
	width: 92%;
	height: 7.6rem/* 580px */;
	margin:0 auto;
	box-shadow: 0px .04rem .28rem .066667rem 
		rgba(96, 192, 255, 0.35);
	border-radius: .133333rem/* 10px */;
}
#lpinfo .nav .inp .mint-cell-wrapper{
	font-size: .373333rem/* 28px */;
}
#lpinfo .nav .post {
	width: 100%;
	padding-top: .2rem;
}
#lpinfo .nav .post .right{
	display: inline-block;
}
#lpinfo .nav .post .left{
	display: inline-block;
}
#lpinfo .nav .post .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: .08rem/* 6px */;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin-left: 1.3rem;
  }
#lpinfo .nav .post .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
#lpinfo .nav .post .avatar-uploader-icon {
    font-size: .373333rem/* 28px */;
    color: #8c939d;
    width: 2.3rem/* 690px */;
    height: 1.866667rem/* 140px */;
    line-height: 1.866667rem/* 140px */;
    text-align: center;
  }
#lpinfo .nav .post .avatar {
    width: 100%;
    height: 1.866667rem/* 140px */;
    display: block;
  }
#lpinfo .nav .post span{
	display: block;
	width: 2.5rem;
	font-size: .373333rem/* 28px */;
	color: #333333;
	padding-left: 1.5rem;
	font-family: FZLTHJW--GB1-0;
	color: #333333;
}
</style>


