<template>
  <div id="test">
  	<CCheader :title="title" :stepname="stepname">
     <p slot="0">
       <inputter label="电话" text="亲输入电话号码"></inputter>
       <inputter label="haoma" text="输入电话号码"></inputter>
       <inputter label="key" text="输入密钥"></inputter>
       <postPic></postPic><postPic></postPic>
     </p> 
     <p slot="1">2</p> 
     <p slot="2">3</p> 
     <p slot="3">4</p> 
     <p slot="4">5</p> 
    </CCheader>
  </div>
</template>

<script>
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import CCheader from '../components/CCheader.vue'
import Inputter from '../components/Inputter.vue'
import postPic from '../components/postPic.vue'
  export default{
  	data(){
  		return{
        title:'企业认证',
        stepname:["第一步","第二步","第三步","第四部","第五步"],
        }
  	},
    components:{
      MintUI,
      CCheader,
      Inputter,
      postPic,
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      send(){
      	console.log('成功');
      },
      back(){
        this.status=this.status-1;
      }
    },
  }
</script>

